import classes from './AppealProcedure.module.scss';
import Wrapper from "../../components/CommonComponents/Wrapper/Wrapper";

const AppealProcedure = () => (
    <Wrapper title={'Порядок обжалования'}>
        <article className={classes.wrapper}>
            <p>Судебный и административный порядок обжалования нормативных правовых актов, решений Главного управления по труду и занятости населения Челябинской области, действий (бездействия) должностных лиц.</p>
            <h3>Административный порядок обжалования нормативных актов и иных решений Главного управления по труду и занятости населения Челябинской области, действий (бездействия)  должностных лиц.</h3>
            <p>Граждане и организации, в случае если они считают, что нарушены их права, свободы и интересы, вправе обратиться непосредственно в вышестоящий в порядке подчиненности орган государственной власти (к должностному лицу, государственному служащему). Такое обращение не лишает их права в дальнейшем на обращение в суд.</p>
            <p>Обращения граждан и организаций с жалобой на решения действия (бездействия) государственного гражданского служащего Челябинской области, замещающего должность в Главном управлении по труду и занятости населения Челябинской области, подаются на имя начальника Главного управления по труду и занятости населения Челябинской области.</p>
            <p>В соответствии с Уставом (Основным Законом) Челябинской области высшим исполнительным органом государственной власти Челябинской области является Правительство Челябинской области.</p>
            <h3>Судебный порядок обжалования нормативных актов и иных решений Главного управления по труду и занятости населения Челябинской области, действий (бездействия)  должностных лиц.</h3>
            <p>В соответствии со статьей 46 Конституции Российской Федерации и главой 25 Гражданского процессуального кодекса Российской Федерации (далее - ГПК РФ) граждане и организации вправе обратиться в суд за защитой своих прав и свобод с заявлением об оспаривании решений, действий (бездействия) органов государственной власти, органов местного самоуправления, должностных лиц, государственных и муниципальных служащих, в результате которых, по мнению указанных лиц, были нарушены их права и свободы или созданы препятствия к осуществлению ими прав и свобод либо на них незаконно возложена какая-либо обязанность или они незаконно привлечены к ответственности.</p>
            <p>Заявление об оспаривании решений, действий (бездействия) органов государственной власти, органов местного самоуправления, должностных лиц, государственных или муниципальных служащих должно отвечать требованиям, предусмотренным абзацем вторым части 1 статьи 247 и статьей 131 ГПК РФ, в части, не противоречащей особенностям производства по делам данной категории, установленным главами 23 и 25 ГПК РФ. В нем, в частности, должно быть указано, какие решения, действия (бездействия), по мнению заявителя, являются незаконными, какие права и свободы нарушены (осуществлению каких прав и свобод созданы препятствия).</p>
            <p>К заявлению об оспаривании решений, действий (бездействия) органов государственной власти, органов местного самоуправления, должностных лиц, государственных и муниципальных служащих, необходимо приобщать копию заявления, документ, подтверждающий уплату государственной пошлины, доверенность или иной документ, удостоверяющий полномочия представителя заявителя, сведения, подтверждающие факт принятия оспариваемых решений, совершение оспариваемых действий или бездействия (часть 1 статьи 246, статья 132 ГПК РФ).</p>
            <p>Согласно части 1 статьи 254 ГПК РФ гражданин, организация вправе обратиться непосредственно в суд или в вышестоящий в порядке подчиненности орган государственной власти, орган местного самоуправления, к должностному лицу, государственному и муниципальному служащему. Таким образом, заявителю принадлежит право выбора порядка (административного, судебного) защиты своих прав и свобод.</p>
            <p>Исходя из положений части 1 статьи 4 и части 12 статьи 256 ГПК РФ срок для обращения с заявлением в суд исчисляется с даты, следующей за днем, когда заявителю стало известно о нарушении его прав и свобод, о создании препятствий к осуществлению его прав и свобод, о возложении обязанности или о привлечении к ответственности.</p>
            <p>Гражданин вправе обратиться в суд с заявлением в течение трех месяцев со дня, когда ему стало известно о нарушении его прав и свобод.</p>
            <p>Граждане, организации и иные лица вправе обратиться в арбитражный суд с заявлением о признании недействующим нормативного правового акта, принятого государственным органом, органом местного самоуправления, иным органом, должностным лицом, если полагают, что оспариваемый нормативный правовой акт или отдельные его положения не соответствуют закону или иному нормативному правовому акту, имеющим большую юридическую силу, и нарушают их права и законные интересы в сфере предпринимательской и иной экономической деятельности, незаконно возлагают на них какие-либо обязанности или создают иные препятствия для осуществления предпринимательской и иной экономической деятельности.</p>
            <h3>Нормативные правовые акты, регулирующие порядок обжалования нормативных актов и иных решений, действий (бездействия)  Главного управления по труду и занятости населения Челябинской областии его должностных лиц:</h3>
            <ul>
                <li>Конституция Российской Федерации;</li>
                <li>Гражданский процессуальный кодекс Российской Федерации от 14 ноября 2002 г. №138-ФЗ;</li>
                <li>Закон Российской Федерации от 27 апреля 1993 г. №4866-I «Об обжаловании в суд действий и решений, нарушающих права и свободы граждан»;</li>
                <li>Арбитражный процессуальный кодекс Российской Федерации от 24 июля 2002 г. №95-ФЗ.</li>
            </ul>
        </article>
    </Wrapper>
);
export default AppealProcedure;